.btn {
    padding: 1rem 2rem;
    font-weight: 700;
    background: rgb(255, 255, 255);
    color: #ff8fc1;
    border-radius: .5rem;
    border-bottom: 2px solid #ff8fc1;
    border-right: 2px solid #ff8fc1;
    border-top: 2px solid white;
    border-left: 2px solid white;
    transition-duration: 1s;
    transition-property: border-top, 
       border-left, 
       border-bottom,
       border-right,
       box-shadow;
   }
   
   .btn:hover {
    border-top: 2px solid #ff73b2;
    border-left: 2px solid #ffbedb;
    border-bottom: 2px solid #ffb2d5;
    border-right: 2px solid #ffa3cc;
    box-shadow: #ff8fc1 2px 2px, #ff8fc1 5px 5px, #ff8fc1 10px 10px;
   }